<template>
  <div id="wrapper">
    <app-nav id="nav" style="display:none;" class="animate__animated animate__fadeInRight print-off"></app-nav>
    <div class="d-flex flex-column animate__animated animate__fadeInDown" id="content-wrapper" style="display:none;">
            <div class="container-fluid mt-4">
                <div class="d-sm-flex justify-content-between align-items-center mb-4 print-off">
                    <burger class="print-off"/>
                    <h3 class="text-dark print-off">   </h3>
                    <header-btns class="print-off"></header-btns>
                </div>
                <hr class="print-off">
                <div class="row">
                    <div class="col-1 mb-1">
                        <label class="text-dark mt-2">  : </label>
                    </div>
                    <div class="col-3 mb-1">
                        <input type="date" class="form-control" v-model="date1">
                    </div>
                </div>
                <div class="row">
                    <div class="col-1 mb-1">
                        <label class="text-dark mt-2">  : </label>
                    </div>
                    <div class="col-3 mb-1">
                        <input type="date" class="form-control" v-model="date2">
                    </div>
                    <button class="btn btn-primary float-right print-off" v-on:click="search()">  </button>
                </div>
                
                <hr>
                <table class="table table-striped table-bordered" style="width: 40% !important;">
                    <thead>
                        <th class="text-center" style="color: black;">  </th>
                        <th class="text-center" style="color: black;">   </th>
                    </thead>
                    <tbody>
                        <tr v-for="(item, index) in solds" :key="index">
                            <td class="text-center" style="color: black;"> {{ item.user }} </td>
                            <td class="text-center" style="color: black;"> {{ item.total | money_filter }} </td>
                        </tr>
                    </tbody>
                </table>
            </div>
            
        </div>
  </div>
</template>

<script>
import auth from '../auth'
    export default {
        name: 'userReport',
        data() {
            return {
                date1: new Date().toISOString().split('T')[0],
                date2: new Date().toISOString().split('T')[0],
                solds: []
            }
        },
        beforeCreate() {
            auth('userReport')
        },
        methods: {
            search() {
                if((this.date1 && this.date2) && (this.date1 <= this.date2)){
                    this.$http.post('/invoice/getUserSoldByDate', {
                        date1: this.date1,
                        date2: this.date2
                    }).then(({data}) => {
                        console.log(data);
                        this.solds = data;
                    })
                }
            }
        },
        filters: {
            money_filter: function(value){
                var arr = value.toString().split("").reverse();
                var arr2 = [];
                for(let i=0; i < arr.length; i++){
                    arr2.unshift(arr[i]);
                    if((i + 1) % 3 == 0){
                        arr2.unshift(",");
                    }
                }
                var number_with_comma = arr2.join("");
                if(number_with_comma.charAt(0) == ','){
                    return number_with_comma.slice(1);
                }
                return number_with_comma;
            }
        }
    }
</script>

<style>
    @media print {
        @page {
            size: 80mm 297mm !important;
            margin-top: 0 !important;
            margin-bottom: 5mm !important;
            margin-left: 0 !important;
            margin-right: 5.8mm !important;
        }
        .print-off {
            display: none !important;
        }
    }
</style>