<template>
    <div id="wrapper">
        <app-nav id="nav" style="display:none;" class="animate__animated animate__fadeInRight no-print"></app-nav>
        <div class="d-flex flex-column animate__animated animate__fadeInDown" id="content-wrapper" style="display:none;">
            <div class="container-fluid mt-4">
                <div class="d-sm-flex justify-content-between align-items-center mb-4">
                    <burger class="no-print"/>
                    <h3 class="text-dark" id="heading">  </h3>
                    <header-btns class="no-print"></header-btns>
                </div>
                <div class="text-right" style="display: none; color: black; margin-bottom: 10px;" id="detail">
                    <hr style="margin-right: -13px; margin-bottom: 7px;">
                    <div v-if="searchType == 'singleDate'">
                        : &nbsp; {{ singleDate }}
                    </div>
                    <div v-if="searchType == 'twoDate'">
                        : &nbsp; {{ date1 }}
                        <br>
                        : &nbsp; {{ date2 }}
                    </div>
                    <div v-if="searchByUser">
                        : &nbsp; {{ (users.find(obj => obj.user_id == user) || {full_name: ''}).full_name }}
                        
                    </div>
                </div>
                <div class="row no-print">

                    <div class="col-xl-3 col-md-5 col-sm-12" style="border-left: 1px solid lightgrey;">
                        <div class="row">
                            <div class="col-12 text-center" :class="{'text-dark': searchType == 'singleDate'}">
                                <input type="radio" class="mx-2" value="singleDate" v-model="searchType">
                                &nbsp;
                                   
                            </div>
                        </div>
                        <hr>
                        <div class="row">
                            <div class="col-12 mb-2">
                                <input type="date" class="form-control" :disabled="searchType != 'singleDate'" v-model="singleDate">
                            </div>
                        </div>
                    </div>

                    <div class="col-xl-6 col-md-7 col-sm-12" style="border-left: 1px solid lightgrey;">
                        <div class="row">
                            <div class="col-12 text-center" :class="{'text-dark': searchType == 'twoDate'}">
                                <input type="radio" class="mx-2" value="twoDate" v-model="searchType">
                                &nbsp;
                                   
                            </div>
                        </div>
                        <hr>
                        <div class="row">
                            <div class="col-1 mt-2 mx-0 px-0">
                                :
                            </div>
                            <div class="col-5 mb-2">
                                <input type="date" class="form-control" :disabled="searchType != 'twoDate'" v-model="date1">
                            </div>
                            <div class="col-1 mt-2 mx-0 px-0">
                                :
                            </div>
                            <div class="col-5 mb-2">
                                <input type="date" class="form-control" :disabled="searchType != 'twoDate'" v-model="date2">
                            </div>
                        </div>
                    </div>

                    <div class="col-xl-3 col-md-12 col-sm-12">
                        <div class="row">
                            <div class="col-12 text-center" :class="{'text-dark': searchByUser}">
                                <input type="checkbox" class="mx-2" v-model="searchByUser">
                                &nbsp;
                                  
                            </div>
                        </div>
                        <hr>
                        <div class="row">
                            <div class="col-12 mb-2">
                                <select class="form-control" :disabled="!searchByUser" v-model="user">
                                    <option value="0"></option>
                                    <option v-for="user in users" :key="user.user_id" :value="user.user_id"> {{ user.full_name }} </option>
                                </select>
                            </div>
                        </div>
                    </div>

                </div>
                <table class="table table-striped table-bordered text-center text-dark mt-2" id="table">
                    <caption class="alert alert-primary text-center" style="display: none;" id="loading">   ... </caption>
                    <thead>
                        <th class="text-center no-print"> # </th>
                        <th class="text-center">  </th>
                        <th class="text-center">   </th>
                        <th class="text-center">  </th>
                        <th class="text-center">   </th>
                    </thead>
                    <tbody>
                        <tr v-for="(food, index) in foods" :key="index">
                            <td class="no-print"> {{ index + 1 }} </td>
                            <td> {{ food.food_name }} </td>
                            <td> {{ food.qty }} </td>
                            <td> {{ food.price }} </td>
                            <td> {{ food.total_price }} </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</template>

<script>
    import auth from '../auth';
    export default {
        data() {
            return {
                searchType: '',
                searchByUser: false,
                singleDate: new Date().toISOString().split('T')[0],
                date1: new Date().toISOString().split('T')[0],
                date2: new Date().toISOString().split('T')[0],
                user: 0,
                users: [],
                foods: []
            }
        },
        beforeCreate() {
            auth('foodsReport');
        },
        created() {
            this.$http.post('/user/getData').then(({data}) => {
                this.users = data;
            });
        },
        methods: {
            getDataBySingleDate() {
                this.foods = [];
                if(this.date1) {
                    document.getElementById('loading').style.display = 'block';
                    this.$http.get('/foods/countFoodsBySingleDate/' + this.singleDate + '/' + this.user).then(({data}) => {
                        this.foods = data;
                    }).finally(() => {
                        document.getElementById('loading').style.display = 'none';
                    });
                }
            },
            getDataByTwoDate() {
                this.foods = [];
                if(this.date1 && this.date2) {
                    document.getElementById('loading').style.display = 'block';
                    this.$http.get('/foods/countFoodsByTwoDate/' + this.date1 + '/' + this.date2 + '/' + this.user).then(({data}) => {
                        this.foods = data;
                    }).finally(() => {
                        document.getElementById('loading').style.display = 'none';
                    });
                }
            }
        },
        watch: {
            searchType() {
                this.foods = [];
            },
            singleDate() {
                this.getDataBySingleDate();
            },
            date1() {
                this.getDataByTwoDate();
            },
            date2() {
                this.getDataByTwoDate();
            },
            searchByUser(value) {
                if(!value) {
                    this.user = 0;
                }
                if(this.searchType == 'singleDate'){
                    this.getDataBySingleDate();
                } else if(this.searchType == 'twoDate') {
                    this.getDataByTwoDate();
                }
            },
            user() {
                if(this.searchType == 'singleDate'){
                    this.getDataBySingleDate();
                } else if(this.searchType == 'twoDate') {
                    this.getDataByTwoDate();
                }
            }
        }
    }
</script>

<style scoped>
    @media print {
        .no-print {
            display: none !important;
        }
        #heading {
            color: black !important;
            font-size: 20px;
            text-align: center;
        }
        #wrapper {
            width: 410px !important;
            max-width: 410px !important;
            margin-right: 0 !important;
        }
        #detail {
            display: block !important;
        }
        table {
            width: 100% !important;
            margin-right: -13px !important;
        }
        th, td {
            color: black !important;
        }
        @page {
            size: 80mm 297mm !important;
            margin-top: 0 !important;
            margin-bottom: 5mm !important;
            margin-left: 0 !important;
            margin-right: 5.8mm !important;
        }
    }
</style>