<template>
    <i class="fa fa-bars btn btn-outline-primary" v-on:click="show_hide()" style="margin-top: -15px;"></i>
</template>

<script>
    export default {
        data() {
            return {}
        },
        methods: {
            show_hide() {
                const elStyle = document.getElementById('nav').style.display;
                if(elStyle == 'block'){
                    document.getElementById('nav').style.display = 'none';
                } else {
                    document.getElementById('nav').style.display = 'block';
                }
            }
        }
    }
</script>